.header {
    padding: 40px 0 10px;
}

.header__conteiner {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header__title {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    font-size: 56px;
    line-height: 65px;
}

.header__subtitle {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    font-size: 26px;
    line-height: 35px;
}

@media (max-width: 767px) {
    .header__title {
        font-size: 32px;
        line-height: 40px;
    }

    .header__subtitle {
        font-size: 16px;
        line-height: 20px;
    }
}